
import { defineComponent } from "vue";

export default defineComponent({
  name: "ColorInput",
  props: {
    modelValue: {
      type: String,
      required: true,
    },
  },
  computed: {
    innerValue: {
      get(): string {
        return this.modelValue;
      },
      set(v: string) {
        this.$emit("update:modelValue", v);
      },
    },
  },
});
